import { Radio, Space, Table } from "antd"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { VList } from 'virtual-table-ant-design'
import Search from "antd/lib/input/Search"
import { useHotkeys } from 'react-hotkeys-hook'
import { getWeaponAsync, weaponList, isLoading} from "./weaponSlice"
import { getColumn, weaponType } from "./column"

let lastIndex = 0
const updateIndex = () => {
  lastIndex++
  return lastIndex
}

export default function Weapon() {
  const o_weapon = useSelector(weaponList)
  const [weapons, setWeapons] = useState(o_weapon[1])

  const loading = useSelector(isLoading)
  const dispatch = useDispatch()
  const searchInput = useRef()
  const [wpnType, setWpnType] = useState(1)
  
  useEffect(() => {
    dispatch(getWeaponAsync())
  }, [dispatch])

  useEffect(() => {
    setWeapons(o_weapon[wpnType])
  }, [o_weapon, wpnType])

  const onSearch = (value) => {
    setWeapons(o_weapon[wpnType].filter(v => v.ko.includes(value)))
  }

  useHotkeys('ctrl+f', (e) => {
    searchInput.current.focus({
      cursor: 'all',
    })
    e.preventDefault();  
  });

  const onKeyDownSearch = (e) => {
    if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {        
        e.preventDefault();         
        searchInput.current.focus({
          cursor: 'all',
        })   
    }    
  }

  const onRow = (q, i) => {
    return {
      onClick: (e) => {
       
      }
    }
  }


  const onChangeWpn = e => {
    setWpnType(e.target.value)
  }
  

  return (
    <>
      <Space style={{ marginBottom: 16 }}>
         <Radio.Group
          options={weaponType}
          onChange={onChangeWpn}
          value={wpnType}
          optionType="button"
        />
        <Search placeholder="무기 명 검색" 
          allowClear onSearch={onSearch} style={{ width: 400 }} 
          onKeyDown={onKeyDownSearch}
          ref={searchInput}/>
      </Space>
     
      <Table columns={getColumn(wpnType, updateIndex)} dataSource={weapons} pagination={false} 
      loading={loading}
      rowKey='id'
      rowClassName='more_detail'
      onRow={onRow}
      scrollToFirstRowOnChange={true}
      scroll={{ y: '80vh' }}
      components={VList({
        height: 2000,
      })}/>
    </>
  )
}