import { createRef, useEffect } from "react"

export default function Sharpness(props) {
  const width = props.width || 92
  const height = props.height || 14
  const type = props.type || 0
  const data = props.data || '00000000000000'

  const sharpness = data.match(/.{1,2}/g)
  const sharp0Ref = createRef()
  const sharp1Ref = createRef()
  const sharp2Ref = createRef()
  
  const getSharpness = (d, init) => {
    let tmp = init
    
    return d.map(v => {
      const ot = Math.max(0, v - tmp)
      tmp = Math.max(0, tmp - v)
      return ot
    })  
  }
  
  

  useEffect(() => {
    const draw = (canvasRef, d) => {
      let rd = d.slice().reverse()
      let c = canvasRef.current
      let ctx = c.getContext('2d')
      ctx.fillStyle = '#BBBBBB'
      ctx.fillRect(0, 0, width, height)
      
      let x = 1
      rd.forEach((v, i) => {
        ctx.fillStyle = color[i]
        const w = (v / 90.0) * (width - 2)
        ctx.fillRect(x, 1, w , height - 2)
        x += w
      })
    }

    const color = ['#FF0000', '#FF9200' ,'#FFFF00', '#00D300', '#0000FF', '#FFFFFF', '#6300FF']

    if (type === 1) {
      draw(sharp0Ref, getSharpness(sharpness, 10))
      draw(sharp1Ref, getSharpness(sharpness, 5))
    }
    
    draw(sharp2Ref, sharpness)
  }, [sharp0Ref, sharp1Ref, sharp2Ref, sharpness, type, height, width])
  
  
  
  return (
    <>
      {type === 1 && <div><canvas ref={sharp0Ref} width={width} height={height}/></div>}
      {type === 1 && <div><canvas ref={sharp1Ref} width={width} height={height}/></div>}
      <div>
        <canvas ref={sharp2Ref} width={width} height={height}/>
      </div>
    </>
  )
}