import { Space, Table, Tag } from "antd"
import Text from "antd/lib/typography/Text"
import { useEffect, useRef, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { VList } from 'virtual-table-ant-design'
import Search from "antd/lib/input/Search"
import { useHotkeys } from 'react-hotkeys-hook'
import { getArmorAsync, isLoading, armorList } from "./armorSlice"

let lastIndex = 0
const updateIndex = () => {
  lastIndex++
  return lastIndex
}

export default function Weapon() {
  const o_armors = useSelector(armorList)
  const [armors, setArmors] = useState(o_armors)
  const loading = useSelector(isLoading)
  const dispatch = useDispatch()
  const searchInput = useRef()

  const parts = ['', '머리', '몸통', '팔', '허리', '다리']
  const slots = ['---', '○--', '○○-', '○○○']
  const columns = [
    {
      title: '방어구',
      key: `armor${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">
        <div>
          {r.type === 1 && <Tag color="green">검사</Tag>}
          {r.type === 2 && <Tag color="green">런너</Tag>}
          {r.gender === 1 && <Tag color="blue">남</Tag>}
          {r.gender === 2 && <Tag color="magenta">여</Tag>}
        </div>
        <Text strong>{r.ko}</Text>
        <Text type="secondary">{r.jp}</Text>
      </Space>,
      filters: [
        {text: '검사', value: 'type.1' },
        {text: '런너', value: 'type.2' },
        {text: '남', value: 'gender.1' },
        {text: '여', value: 'gender.2' }
      ],
      onFilter: (value, record) => {
        let [k, v] = value.split('.')
        return record[k] === Number(v)
      },
      width: 240
    },
    {
      title: '부위',
      key: `parts${updateIndex()}`,
      render: (_, r) => parts[r.parts],
      filters: [
        {text: '머리', value: 1 },
        {text: '몸통', value: 2 },
        {text: '팔', value: 3 },
        {text: '허리', value: 4 },
        {text: '다리', value: 5 },
      ],
      onFilter: (value, record) => record.parts === value,
      width: 110
    },
    {
      title: '슬롯',
      key: `slot${updateIndex()}`,
      render: (_, r) => slots[r.slot],
      filters: [
        {text: '---', value: 0 },
        {text: '○--', value: 1 },
        {text: '○○-', value: 2 },
        {text: '○○○', value: 3 }
      ],
      onFilter: (value, record) => record.slot === value,
      width: 110
    },
    {
      title: '방어 / 최대',
      key: `def${updateIndex()}`,
      render: (_, r) => r.def + ' / ' + r.maxdef,
      width: 110
    },
    {
      title: '불 내성',
      key: `rf${updateIndex()}`,
      dataIndex: 'res_fire',
      width: 110
    },
    {
      title: '물 내성',
      key: `rw${updateIndex()}`,
      dataIndex: 'res_water',
      width: 110
    },
    {
      title: '번개 내성',
      key: `rt${updateIndex()}`,
      dataIndex: 'res_thunder',
      width: 110
    },
    {
      title: '얼음 내성',
      key: `ri${updateIndex()}`,
      dataIndex: 'res_ice',
      width: 110
    },
    {
      title: '용 내성',
      key: `rd${updateIndex()}`,
      dataIndex: 'res_dragon',
      width: 110
    },
    {
      title: '재료',
      key: `ing${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">{r.ing && r.ing.map(v => <Space key={v.key_id}><Text>{v.ko} / {v.jp}</Text><Text>{v.qty}</Text></Space>)}</Space>
    },
    {
      title: '스킬',
      key: `skill${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">{r.skill && r.skill.map(v => <Space key={v.key_id}><Text>{v.ko} / {v.jp}</Text><Text>{v.pt > 0 ? '+' + v.pt : v.pt}</Text></Space>)}</Space>
    },
    {
      title: '가격',
      key: `price${updateIndex()}`,
      dataIndex: 'price',
      width: 110
    }
  ]

  useEffect(() => {
    dispatch(getArmorAsync())
  }, [dispatch])

  useEffect(() => {
    setArmors(o_armors)
  }, [o_armors])

  const onSearch = (value) => {
    setArmors(o_armors.filter(v => v.ko.includes(value)))
  }

  useHotkeys('ctrl+f', (e) => {
    searchInput.current.focus({
      cursor: 'all',
    })
    e.preventDefault();  
  });

  const onKeyDownSearch = (e) => {
    if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {        
        e.preventDefault();         
        searchInput.current.focus({
          cursor: 'all',
        })   
    }    
  }

  const onRow = (q, i) => {
    return {
      onClick: (e) => {
      }
    }
  }


  

  return (
    <>
      <Space style={{ marginBottom: 16 }}>
        <Search placeholder="방어구 명 검색" 
          allowClear onSearch={onSearch} style={{ width: 400 }} 
          onKeyDown={onKeyDownSearch}
          ref={searchInput}/>
      </Space>
      <Table columns={columns} dataSource={armors} pagination={false} 
      loading={loading}
      rowKey='id'
      rowClassName='more_detail'
      onRow={onRow}
      scrollToFirstRowOnChange={true}
      scroll={{ y: '80vh' }}
      components={VList({
        height: 1000,
      })}/>
    </>
  )
}