import { Space } from "antd"
import Text from "antd/lib/typography/Text"
import Sharpness from "../../component/Sharpness"
const slots = ['---', '○--', '○○-', '○○○']
const note = [
  require('../../asset/note/1.png').default,
  require('../../asset/note/2.png').default,
  require('../../asset/note/3.png').default,
  require('../../asset/note/4.png').default,
  require('../../asset/note/5.png').default,
  require('../../asset/note/6.png').default,
  require('../../asset/note/7.png').default,
  require('../../asset/note/8.png').default,
]
export function getColumn(type, updateIndex) {
  const def_columns = [
    {
      title: '무기',
      key: `wname${updateIndex()}`,
      render: (_, r) => <Space direction="vertical"><Text strong>{r.ko}</Text><Text type="secondary">{r.jp}</Text></Space>
    },
    {
      title: '레어도',
      key: `wrare${updateIndex()}`,
      dataIndex: 'rare',
      width: 80
    },
    {
      title: '공격',
      key: `watk${updateIndex()}`,
      dataIndex: 'atk',
      width: 80
    },
    {
      title: '속성',
      key: `watk${updateIndex()}`,
      render: (_, r) => {
        let a = ''
        if (r.sp1 !== '---')
          a += `${r.sp1} ${r.sp1_pt}`
        if (r.sp2 !== '---')
          a += ` / ${r.sp2} ${r.sp2_pt}`
        return a
      },
      width: 200
    },
    {
      title: '회심',
      key: `waff${updateIndex()}`,
      render: (_,r) => (r.affinity > 0 ? '+' : '') + Math.floor(r.affinity * 100) + '%',
      width: 80
    },
    {
      title: '방어',
      key: `wdef${updateIndex()}`,
      render: (_,r) => (r.def > 0 ? '+' : '') + r.def,
      width: 80
    },
    {
      title: '슬롯',
      key: `wslot${updateIndex()}`,
      render: (_, r) => slots[r.slot],
      width: 80
    },
    {
      title: '예리도',
      key: `wsharp${updateIndex()}`,
      render: (_, r) => <Sharpness width={256} data={r.sharp} type={r.sharpp1} />,
      width: 300
    },
    {
      title: '생산',
      key: `wpro${updateIndex()}`,
      dataIndex: 'pro_px',
      width: 80
    },
    {
      title: '강화',
      key: `wlv${updateIndex()}`,
      dataIndex: 'lv_px',
      width: 80
    },
    {
      title: '생산 재료',
      key: `ping${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">{r.ping && r.ping.map(v => <Space key={v.key_id}><Text>{v.ko} / {v.jp}</Text><Text>{v.qty}</Text></Space>)}</Space>
    },
    {
      title: '업글 재료',
      key: `uing${updateIndex()}`,
      render: (_, r) => <Space direction="vertical">{r.uing && r.uing.map(v => <Space key={v.key_id}><Text>{v.ko} / {v.jp}</Text><Text>{v.qty}</Text></Space>)}</Space>
    },
  ]

  switch(type) {
    case 6:
      // 피리
      return [].concat(
              def_columns.slice(0,7),
              [{ 
                title: '선율',
                key: `wnote${updateIndex()}`,
                render: (_, r) => <Space>
                  <img src={note[r.note1-1]} alt='선율1'/>
                  <img src={note[r.note2-1]} alt='선율2'/>
                  <img src={note[r.note3-1]} alt='선율3'/>
                  </Space>,
                  width: 100
              }],
              def_columns.slice(7))
    case 8:
      // -건-
      return [].concat(
        def_columns.slice(0,7),
        [{ 
          title: '차지 공격',
          key: `wglshot${updateIndex()}`,
          dataIndex: 'gl_shottype',
          width: 100
        }],
        def_columns.slice(7))
    case 9:
    case 10:
      // 차액/슬액
      return [].concat(
        def_columns.slice(0,7),
        [{ 
          title: '장착병',
          key: `waxep${updateIndex()}`,
          dataIndex: 'axephial',
          width: 100
        }],
        def_columns.slice(7))
    case 11:
      // 조충곤
      return [].concat(
        def_columns.slice(0,7),
        [{ 
          title: '유형',
          key: `wisk${updateIndex()}`,
          dataIndex: 'isktype',
          width: 100
        }],
        def_columns.slice(7))
    case 12:
    case 13:
      // 헤보 / 라보
      return [].concat(
        def_columns.slice(0,7),
        [{ 
          title: '재장전',
          key: `wrel${updateIndex()}`,
          dataIndex: 'grspeed',
          width: 100
        },
        { 
          title: '반동',
          key: `wrec${updateIndex()}`,
          dataIndex: 'recoil',
          width: 100
        },
        { 
          title: '치우침',
          key: `wstd${updateIndex()}`,
          dataIndex: 'steady',
          width: 100
        }],
        def_columns.slice(8))
    case 14:
      // 활
      return [].concat(
        def_columns.slice(0,7),
        [{ 
          title: '곡사',
          key: `wbs${updateIndex()}`,
          dataIndex: 'bowshot',
          width: 100
        },
        { 
          title: '차지 공격1',
          key: `wbt1${updateIndex()}`,
          dataIndex: 'bowtype1',
          width: 100
        },
        { 
          title: '차지 공격2',
          key: `wbt2${updateIndex()}`,
          dataIndex: 'bowtype2',
          width: 100
        },
        { 
          title: '차지 공격3',
          key: `wbt3${updateIndex()}`,
          dataIndex: 'bowtype3',
          width: 100
        },
        { 
          title: '차지 공격4',
          key: `wbt4${updateIndex()}`,
          dataIndex: 'bowtype4',
          width: 100
        },],
        def_columns.slice(8))
    default:
      return def_columns
  }
}

export const weaponType = [
  {value: 1, label: '대검'},
  {value: 2, label: '태도'},
  {value: 3, label: '한손검'},
  {value: 4, label: '쌍검'},
  {value: 5, label: '해머'},
  {value: 6, label: '수렵피리'},
  {value: 7, label: '랜스'},
  {value: 8, label: '건랜스'},
  {value: 9, label: '슬래시엑스'},
  {value: 10, label: '차지엑스'},
  {value: 11, label: '조충곤'},
  {value: 12, label: '라이트보우건'},
  {value: 13, label: '헤비보우건'},
  {value: 14, label: '활'},
]