import Quest from "../features/quest/Quest";
import Item from '../features/item/Item';
import Armor from '../features/armor/Armor';
import Weapon from '../features/weapon/Weapon';

const routes = [
  {
    path: '/quests',
    label: '퀘스트',
    component: Quest
  },
  {
    path: '/items',
    label: '아이템',
    component: Item
  },
  {
    path: '/armors',
    label: '방어구',
    component: Armor
  },
  {
    path: '/weapons',
    label: '무기',
    component: Weapon
  }
]

export default routes;