import _ from 'lodash';
import { getItems, getQuestLv, getQuestQuery, getQuestRewards, getItemTo, getArmor, getArmorIng, getArmorSkill, getWeapon, getWeaponUIng, getWeaponPIng } from './const_query';
const initSqlJs = require('sql.js');
let _db = undefined;

async function getDB() {
  if (_db !== undefined) return _db
  const sqlPromise = initSqlJs({
    locateFile: file => `/mhxx/${file}`
  });
  const dataPromise = fetch("/mhxx/mhxx.db").then(res => res.arrayBuffer());
  const [SQL, buf] = await Promise.all([sqlPromise, dataPromise])
  _db = new SQL.Database(new Uint8Array(buf));

  return _db
}

export function getQuestsFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let quests = {}
    db.each(getQuestQuery,
        (r) => { quests[r.id] = r },
        () => {
          let rewards = []
          db.each(getQuestRewards, 
              (r) => rewards.push(r), () => {
                let nr = _.reduce(rewards, (p, c) => {
                  var qid = p[c.id] = p[c.id] || {A: [], B: [], C: [], D: []}
                  var type = qid[c.type] = qid[c.type] || []

                  type.push(_.omit(c, 'id'))

                  return p
                }, {})
                
                let result = _.chain(quests).mapValues(v => {
                  v.rewards = nr[v.id]
                  v.rewards_span = 24 / (
                    (v.rewards.A.length > 0 ? 1 : 0) +
                    (v.rewards.B.length > 0 ? 1 : 0) +
                    (v.rewards.C.length > 0 ? 1 : 0) +
                    (v.rewards.D.length > 0 ? 1 : 0))
                  return v
                }).values().value()

                resolve(result)
              })
        })
    
  })
}

export function getQuestLvFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let lvs = []
    db.each(getQuestLv,
      (r) => lvs.push(r.lv), 
      () => resolve(lvs))
  })
}

export function getItemsFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let items = []
    db.each(getItems, 
      (r) => items.push(r), 
      async () => {
        let its = _.keyBy(items, 'id')
        let detail = await getItemNested()
        detail.forEach((v) => {
          const k = v.key
          Object.keys(v.result).forEach(i => {
            its[i][k] = v.result[i]
          })
        })
        
        resolve(_.values(its))
      })

  })
}

async function getItemNested() {
  const promises = Object.keys(getItemTo).map(k => {
    return new Promise(async (resolve, reject) => {
      const db = await getDB()
      let results = []
      db.each(getItemTo[k], r => results.push(r), () => resolve({key: k, result: _.groupBy(results, 'id')}))
    })
  })


  
  return await Promise.all(promises)
}

export function getArmorFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let armors = {}
    db.each(getArmor, 
      r => armors[r.id] = r,
      async () => {
        let ings = await getArmorIngNested()
        let skills = await getArmorSkillNested()
        Object.keys(ings).forEach(i => {
          armors[i].ing = ings[i]
        })
        Object.keys(skills).forEach(i => {
          armors[i].skill = skills[i]
        })
        resolve(_.values(armors))
      })
  })
}

async function getArmorIngNested() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let ais = []
    db.each(getArmorIng, r => ais.push(r), () => resolve(_.groupBy(ais, 'id')))
  })
}

async function getArmorSkillNested() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let skills = []
    db.each(getArmorSkill, r => skills.push(r), () => resolve(_.groupBy(skills, 'id')))
  })
}

export function getWpnFromDB() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let weapon = {}
    db.each(getWeapon,
      r => weapon[r.id] = r,
      async () => {
        let pings = await getWeaponPIngNested()
        let uings = await getWeaponUIngNested()
        Object.keys(pings).forEach(i => {
          weapon[i].ping = pings[i]
        })
        Object.keys(uings).forEach(i => {
          weapon[i].uing = uings[i]
        })

        resolve(_.chain(weapon).values().groupBy('type').value())
      })
  })
}

async function getWeaponPIngNested() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let ais = []
    db.each(getWeaponPIng, r => ais.push(r), () => resolve(_.groupBy(ais, 'id')))
  })
}

async function getWeaponUIngNested() {
  return new Promise(async (resolve, reject) => {
    const db = await getDB()
    let skills = []
    db.each(getWeaponUIng, r => skills.push(r), () => resolve(_.groupBy(skills, 'id')))
  })
}
