export const getQuestQuery = `
SELECT 
  Q.Qst_ID AS id,
  QN.Qst_Name_4 AS ko, QN.Qst_Name_0 AS en, QN.Qst_Name_3 AS jp,
  Q.QstTalk AS talk, QL.Qst_Lv_4 AS lv, QK.Qst_Key_4 AS key, QT.Qst_Type_4 AS type,
  LN.Loc_Name_4 AS map, G.Qst_Goal_4 AS goal, G2.Qst_Goal2_4 AS goal2,
  Q.Time AS time, Q.Fee AS contract, Q.Reward AS reward,
  Q.HRP AS hrp, Q.Reward2 AS reward2, Q.HRP2 AS hrp2,
  MN1.Mon_Name_4 AS mon1, MN2.Mon_Name_4 AS mon2,
  MN3.Mon_Name_4 AS mon3, MN4.Mon_Name_4 AS mon4, MN5.Mon_Name_4 AS mon5,
  MN6.Mon_Name_4 AS mon6, MN7.Mon_Name_4 AS mon7, MN8.Mon_Name_4 AS mon8
FROM DB_Qst AS Q 
  INNER JOIN ID_Qst_Name AS QN ON QN.Qst_ID = Q.Qst_ID 
  INNER JOIN ID_Qst_Lv AS QL ON QL.Qst_Lv_ID = Q.Qst_Lv_ID
  INNER JOIN ID_Qst_Key AS QK ON QK.Qst_Key_ID = Q.Qst_Key_ID
  INNER JOIN ID_Qst_Type AS QT ON QT.Qst_Type_ID = Q.Qst_Type_ID
  INNER JOIN ID_Loc_Name AS LN ON LN.Loc_ID = Q.Loc_ID
  INNER JOIN ID_Qst_Goal AS G ON G.Qst_ID = Q.Qst_ID
  INNER JOIN ID_Qst_Goal2 AS G2 ON G2.Qst_ID = Q.Qst_ID
  INNER JOIN ID_Mon_Name AS MN1 ON MN1.Mon_ID = Q.Mon1_ID
  INNER JOIN ID_Mon_Name AS MN2 ON MN2.Mon_ID = Q.Mon2_ID
  INNER JOIN ID_Mon_Name AS MN3 ON MN3.Mon_ID = Q.Mon3_ID
  INNER JOIN ID_Mon_Name AS MN4 ON MN4.Mon_ID = Q.Mon4_ID
  INNER JOIN ID_Mon_Name AS MN5 ON MN5.Mon_ID = Q.Mon5_ID
  INNER JOIN ID_Mon_Name AS MN6 ON MN6.Mon_ID = Q.Mon6_ID
  INNER JOIN ID_Mon_Name AS MN7 ON MN7.Mon_ID = Q.Mon7_ID
  INNER JOIN ID_Mon_Name AS MN8 ON MN8.Mon_ID = Q.Mon8_ID`

export const getQuestRewards = `
SELECT
  IQ.Qst_ID AS id, IQ.ID AS key_id, Itm_Name_4 AS ko, Itm_Name_3 AS jp, 
  Itm_Name_0 AS en, IQ.Qty AS qty, IQ.Pct as percent, IQ.Type AS type
FROM DB_ItmtoQst AS IQ 
  INNER JOIN ID_Itm_Name AS I ON I.Itm_ID = IQ.Itm_ID`

export const getQuestLv = `
SELECT 
  DISTINCT QL.Qst_Lv_4 AS lv 
FROM DB_Qst AS Q
  INNER JOIN ID_Qst_Lv AS QL ON QL.Qst_Lv_ID = Q.Qst_Lv_ID
  ORDER BY Q.Qst_Lv_ID`

export const getItems = `
SELECT 
  I.Itm_ID AS id, ITN.Itm_Name_4 AS ko, ITN.Itm_Name_3 AS jp, ITN.Itm_Name_0 AS en,
	I.Rare AS rare, I.Max AS max, I.Sell AS sell, I.Buy AS buy
FROM DB_Itm AS I
	INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = I.Itm_ID 
WHERE I.Itm_ID <= 2880
ORDER BY I.Itm_ID `

export const getItemTo = {
  quest: `
  SELECT 
    I.Itm_ID AS id, ITQ.ID AS key_id, ITQ.Qty AS qty, IQN.Qst_Name_4 AS ko, IQL.Qst_Lv_4 AS lv,
    IQN.Qst_Name_3 AS jp, IQN.Qst_Name_0 AS en, ITQ.Pct AS percent, ITQ.Type AS type
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoQst AS ITQ ON ITQ.Itm_ID = I.Itm_ID
    INNER JOIN DB_Qst AS Q ON Q.Qst_ID = ITQ.Qst_ID
    INNER JOIN ID_Qst_Lv AS IQL ON IQL.Qst_Lv_ID = Q.Qst_Lv_ID
    INNER JOIN ID_Qst_Name AS IQN ON IQN.Qst_ID = ITQ.Qst_ID`,
  weapon: `
  SELECT 
    IT.Itm_ID AS id, ITTW.ID AS key_id, ITTW.Qty AS qty, IIF(ITTW.Type == 'U', '강화', '생산') AS type,
    IWN.Wpn_Name_4 AS ko, IWN.Wpn_Name_3 AS jp, IWN.Wpn_Name_0 AS en
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoWpn AS ITTW ON ITTW.Itm_ID = IT.Itm_ID
    INNER JOIN ID_Wpn_Name AS IWN ON IWN.Wpn_ID = ITTW.Wpn_ID`,
  armor:  `
  SELECT 
    IT.Itm_ID AS id, ITTA.ID AS key_id, ITTA.Qty AS qty, 
    IAN.Amr_Name_4 AS ko, IAN.Amr_Name_3 AS jp, IAN.Amr_Name_0 AS en
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoAmr AS ITTA ON ITTA.Itm_ID = IT.Itm_ID
    INNER JOIN ID_Amr_Name AS IAN ON IAN.Amr_ID = ITTA.Amr_ID`,
  jew: `
  SELECT 
    IT.Itm_ID AS id, ITTJ.ID AS key_id, ITTJ.Qty AS qty, 
    ITN.Itm_Name_4 AS ko, ITN.Itm_Name_3 AS jp, ITN.Itm_Name_0 AS en
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoJew AS ITTJ ON ITTJ.Itm_ID = IT.Itm_ID
    INNER JOIN DB_Jew AS J ON J.Jew_ID = ITTJ.Jew_ID
    INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = J.Itm_ID`,
  recipe: `
  SELECT 
    I.Itm_ID AS id, ITC.ItmC_ID AS result, ITC.Cbo_ID AS key_id,
    IA.Itm_Name_4 AS ing1_ko, IA.Itm_Name_3 AS ing1_jp, IA.Itm_Name_0 AS ing1_en,
    IB.Itm_Name_4 AS ing2_ko, IB.Itm_Name_3 AS ing2_jp, IB.Itm_Name_0 AS ing2_en,
    IC.Itm_Name_4 AS res_ko, IC.Itm_Name_3 AS res_jp, IC.Itm_Name_0 AS res_en,
    ITC.Succ AS succ, ITC.qty AS qty
  FROM DB_Itm AS I 
    INNER JOIN DB_ItmtoCboU AS ITC ON ITC.ItmC_ID = I.Itm_ID or ITC.ItmB_ID = I.Itm_ID or ITC.ItmC_ID = I.Itm_ID
    INNER JOIN ID_Itm_Name AS IA ON IA.Itm_ID = ITC.ItmA_ID
    INNER JOIN ID_Itm_Name AS IB ON IB.Itm_ID = ITC.ItmB_ID
    INNER JOIN ID_Itm_Name AS IC ON IC.Itm_ID = ITC.ItmC_ID
  GROUP BY ITC.Cbo_ID, I.Itm_ID ORDER BY I.Itm_ID`,
  insect: `
  SELECT 
    I.Itm_ID AS id, ISK.ID AS key_id, ITN.Itm_Name_4 AS ing_ko, ITN.Itm_Name_3 AS ing_jp, ITN.Itm_Name_0 AS ing_en,
    ISKN.ISKInsect_Name_4 AS ins_ko, ISKN.ISKInsect_Name_3 AS ins_jp, ISKN.ISKInsect_Name_0 AS ins_en, 
    ISK.Qty AS qty
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoISKinsect AS ISK ON ISK.Itm_ID = I.Itm_ID
    INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = I.Itm_ID
    INNER JOIN ID_ISKinsect_Name AS ISKN ON ISKN.ISKinsect_ID = ISK.ISKinsect_ID`,
  map: `
  SELECT 
    I.Itm_ID AS id, ITL.ID AS key_id, ILN.Loc_Name_4 AS map, ILA.Loc_Ara_4 AS area, ILG.Loc_GetType_4 AS gettype, IR.Rank_4 AS rank
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoLoc AS ITL ON ITL.Itm_ID = I.Itm_ID
    INNER JOIN ID_Loc_Name AS ILN ON ILN.Loc_ID = ITL.Loc_ID
    INNER JOIN ID_Loc_Ara AS ILA ON ILA.Loc_Ara_ID = ITL.Loc_Ara_ID
    INNER JOIN ID_Loc_GetType AS ILG ON ILG.Loc_GetType_ID = ITL.Loc_GetType_ID
    INNER JOIN ID_Rank AS IR ON IR.Rank_ID = ITL.Rank_ID`,
  grandpa: `
  SELECT
    I.Itm_ID AS id, ITV.Vet_ID AS key_id, ITV.Pct AS percent, ILN.Loc_Name_4 AS map, 
    ION.Itm_Name_4 AS io_ko, ION.Itm_Name_3 AS io_jp, ION.Itm_Name_0 AS io_en,
    IIN.Itm_Name_4 AS ii_ko, IIN.Itm_Name_3 AS ii_jp, IIN.Itm_Name_0 AS ii_en
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoVet AS ITV ON ITV.ItmOut_ID = I.Itm_ID OR ITV.ItmIn_ID = I.Itm_ID
    INNER JOIN ID_Itm_Name AS ION ON ION.Itm_ID = ITV.ItmOut_ID
    INNER JOIN ID_Itm_Name AS IIN ON IIN.Itm_ID = ITV.ItmIn_ID
    INNER JOIN ID_Loc_Name AS ILN ON ILN.Loc_ID = ITV.Loc_ID`,
  hfh: `
  SELECT 
    I.Itm_ID AS id, IHFH.ID AS key_id, IHFH.Qty AS qty, IHFH.Pct AS percent, IHFH.Type AS type,
    HFHN.Hfh_Name_4 AS hfh_ko, HFHN.Hfh_Name_3 AS hfh_jp, HFHN.Hfh_Name_0 AS hfh_en,
    IQL.Qst_Lv_4 AS lv
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoHfh AS IHFH ON IHFH.Itm_ID = I.Itm_ID
    INNER JOIN DB_Hfh AS HFH ON HFH.Hfh_ID = IHFH.Hfh_ID
    INNER JOIN ID_Hfh_Name AS HFHN ON HFHN.Hfh_ID = HFH.Hfh_ID
    INNER JOIN ID_Qst_Lv AS IQL ON IQL.Qst_Lv_ID = HFH.Lv_ID`,
  mon: `
  SELECT 
    I.Itm_ID AS id, ITM.ID AS key_id, ITM.Qty AS qty, ITM.Pct AS percent, IMN.Mon_Name_4 AS mon,
    IR.Rank_4 AS lv, IMGIT.Mon_GetItmType_4 AS way
  FROM DB_Itm AS I
    INNER JOIN DB_ItmtoMon AS ITM ON ITM.Itm_ID = I.Itm_ID
    INNER JOIN ID_Mon_Name AS IMN ON IMN.Mon_ID = ITM.Mon_ID
    INNER JOIN ID_Rank AS IR ON IR.Rank_ID = ITM.Rank_ID
    INNER JOIN ID_Mon_GetItmType AS IMGIT ON IMGIT.Mon_GetItmType_ID = ITM.Mon_GetItmType_ID`,
  peliarm: `
  SELECT 
    IT.Itm_ID AS id, ITTPA.ID AS key_id, ITTPA.Qty AS qty,
    IPAN.PeliAmr_Name_4 AS ko, IPAN.PeliAmr_Name_3 AS jp, IPAN.PeliAmr_Name_0 AS en
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoPeliAmr AS ITTPA ON ITTPA.Itm_ID = IT.Itm_ID
    INNER JOIN ID_PeliAmr_Name AS IPAN ON IPAN.PeliAmr_ID = ITTPA.PeliAmr_ID`,
  peliwp: `
  SELECT 
    IT.Itm_ID AS id, ITTPW.ID AS key_id, ITTPW.Qty AS qty,
    IPWN.PeliWpn_Name_4 AS ko, IPWN.PeliWpn_Name_3 AS jp, IPWN.PeliWpn_Name_0 AS en
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoPeliWpn AS ITTPW ON ITTPW.Itm_ID = IT.Itm_ID
    INNER JOIN ID_PeliWpn_Name AS IPWN ON IPWN.PeliWpn_ID = ITTPW.PeliWpn_ID`,
  peliteam: `
  SELECT 
    IT.Itm_ID AS id, ITTPT.PeliTeam_ID AS key_id, ITTPT.Qty AS qty, ITTPT.Pct AS percent, 
    IPTZ.PeliTeam_Zone_4 AS zone, IR.Rank_4 AS lv, ITTPT.Icon AS icon
  FROM DB_Itm AS IT 
    INNER JOIN DB_ItmtoPeliTeam AS ITTPT ON ITTPT.Itm_ID = IT.Itm_ID
    INNER JOIN ID_PeliTeam_Zone AS IPTZ ON IPTZ.PeliTeam_Zone_ID = ITTPT.Zone_ID
    INNER JOIN ID_Rank AS IR ON IR.Rank_ID = ITTPT.Rank_ID`
}

export const getArmor = `
SELECT
	AMR.Amr_ID AS id, AMN.Amr_Name_4 AS ko, AMN.Amr_Name_3 AS jp, AMN.Amr_Name_0 AS en, AMR.Rare AS rare,
	AMR.BorG AS type, AMR.MorF AS gender, AMR.Part AS parts, AMR.Slot AS slot, AMR.Def AS def, AMR.MaxDef AS maxdef,
	AMR.Price AS price, AMR.Res_Fire AS res_fire, AMR.Res_Water AS res_water, AMR.Res_Thunder AS res_thunder,
	AMR.Res_Ice AS res_ice, AMR.Res_Dragon AS res_dragon
FROM DB_Amr AS AMR 
	INNER JOIN ID_Amr_Name AS AMN ON AMN.Amr_ID = AMR.Amr_ID`

export const getArmorIng = `
SELECT 
	A.Amr_ID AS id, ITA.ID AS key_id, ITN.Itm_Name_4 AS ko, ITN.Itm_Name_3 AS jp, ITN.Itm_Name_0 AS en,
	ITA.qty AS qty
FROM DB_Amr AS A
	INNER JOIN DB_ItmtoAmr AS ITA ON ITA.Amr_ID = A.Amr_ID
	INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = ITA.Itm_ID`

export const getArmorSkill = `
SELECT 
	A.Amr_ID AS id, STA.ID AS key_id, STN.SklTree_Name_4 AS ko, 
	STN.SklTree_Name_3 AS jp, STN.SklTree_Name_0 AS en, STA.Pt AS pt
FROM DB_Amr AS A
	INNER JOIN DB_SklTreetoAmr AS STA ON STA.Amr_ID = A.Amr_ID
	INNER JOIN ID_SklTree_Name AS STN ON STN.SklTree_ID = STA.SklTree_ID`

export const getWeapon = `
SELECT
	W.Wpn_ID AS id, W.Wpn_Type_ID AS type, WN.Wpn_Name_4 AS ko, WN.Wpn_Name_3 AS jp, WN.Wpn_Name_0 AS en, W.Rare AS rare, W.Atk AS atk, SP1.Wpn_SPAtk_4 AS sp1, W.SpAtk1_Pt AS sp1_pt,
	SP2.Wpn_SPAtk_4 AS sp2, W.SpAtk2_Pt AS sp2_pt, W.Affinity AS affinity, W.Def AS def,
	W.Slot AS slot, W.ProPx AS pro_px, W.LvUpPx AS lv_px, W.Sharp AS sharp, W.SharpP1 AS sharpp1, GLSHOT.Wpn_ShotType_4 AS gl_shottype,
	AXEPHI.Wpn_AxePhial_4 AS axephial, W.HHNote1_ID AS note1, W.HHNote2_ID AS note2, W.HHNote3_ID AS note3, ISKTYPE.Wpn_ISKinsectType_4 AS isktype,
	GUNRELOAD.Wpn_GunReloadSpd_4 AS grspeed, GUNSTEAD.Wpn_GunSteadiness_4 AS steady, GUNRECOIL.Wpn_GunRecoil_4 AS recoil, 
	BOWSHOT.Wpn_BowShot_4 AS bowshot, BOWSHOTT1.Wpn_ShotType_4 AS bowtype1, BOWSHOTT2.Wpn_ShotType_4 AS bowtype2, BOWSHOTT3.Wpn_ShotType_4 AS bowtype3,
	BOWSHOTT4.Wpn_ShotType_4 AS bowtype4
FROM DB_Wpn AS W
	INNER JOIN ID_Wpn_Name AS WN ON WN.Wpn_ID = W.Wpn_ID
	INNER JOIN ID_Wpn_SpAtk AS SP1 ON SP1.Wpn_SpAtk_ID = W.SpAtk1_ID
	INNER JOIN ID_Wpn_SpAtk AS SP2 ON SP2.Wpn_SpAtk_ID = W.SpAtk2_ID
	INNER JOIN ID_Wpn_ShotType AS GLSHOT ON GLSHOT.Wpn_ShotType_ID = W.GLShotType_ID
	INNER JOIN ID_Wpn_AxePhial AS AXEPHI ON AXEPHI.Wpn_AxePhial_ID = W.AxePhial_ID
	INNER JOIN ID_Wpn_ISKinsectType AS ISKTYPE ON ISKTYPE.Wpn_ISKinsectType_ID = W.ISKinsectType_ID
	INNER JOIN ID_Wpn_GunReloadSpd AS GUNRELOAD ON GUNRELOAD.Wpn_GunReloadSpd_ID = W.GunReloadSpd_ID
	INNER JOIN ID_Wpn_GunSteadiness AS GUNSTEAD ON GUNSTEAD.Wpn_GunSteadiness_ID = W.GunSteadiness_ID
	INNER JOIN ID_Wpn_GunRecoil AS GUNRECOIL ON GUNRECOIL.Wpn_GunRecoil_ID = W.GunRecoil_ID
	INNER JOIN ID_Wpn_BowShot AS BOWSHOT ON BOWSHOT.Wpn_BowShot_ID = W.BowShot_ID
	INNER JOIN ID_Wpn_ShotType AS BOWSHOTT1 ON BOWSHOTT1.Wpn_ShotType_ID = W.BowShotType1_ID
	INNER JOIN ID_Wpn_ShotType AS BOWSHOTT2 ON BOWSHOTT2.Wpn_ShotType_ID = W.BowShotType2_ID
	INNER JOIN ID_Wpn_ShotType AS BOWSHOTT3 ON BOWSHOTT3.Wpn_ShotType_ID = W.BowShotType3_ID
	INNER JOIN ID_Wpn_ShotType AS BOWSHOTT4 ON BOWSHOTT4.Wpn_ShotType_ID = W.BowShotType4_ID`

export const getWeaponPIng = `
SELECT
	W.Wpn_ID AS id, ITW.ID AS key_id, ITN.Itm_Name_4 AS ko, ITN.Itm_Name_3 AS jp, ITN.Itm_Name_0 AS en, ITW.qty AS qty
FROM DB_Wpn AS W
	INNER JOIN DB_ItmtoWpn AS ITW ON ITW.Wpn_ID = W.Wpn_ID
	INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = ITW.Itm_ID
WHERE ITW.Type='P'`


export const getWeaponUIng = `
SELECT
	W.Wpn_ID AS id, ITW.ID AS key_id, ITN.Itm_Name_4 AS ko, ITN.Itm_Name_3 AS jp, ITN.Itm_Name_0 AS en, ITW.qty AS qty
FROM DB_Wpn AS W
	INNER JOIN DB_ItmtoWpn AS ITW ON ITW.Wpn_ID = W.Wpn_ID
	INNER JOIN ID_Itm_Name AS ITN ON ITN.Itm_ID = ITW.Itm_ID
WHERE ITW.Type='U'`
  