import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { getArmorFromDB } from '../../app/db';

const initialState = {
  armors: [],
  loading: false
}

export const getArmorAsync = createAsyncThunk(
  'armors/getArmor',
  async () => {
    const armors = await getArmorFromDB()

    return armors
  }
)

export const armorSlice = createSlice({
  name: 'armor',
  initialState,
  reducers: {
    getArmor: (state) => {
      state.armors = []
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(getArmorAsync.pending, (state) => {
        state.loading = true;
      })
      .addCase(getArmorAsync.fulfilled, (state, action) => {
        state.armors = action.payload
        state.loading = false
      })
  },
})

export const { getArmor }  = armorSlice.actions

export const armorList = (state) => state.armor.armors
export const isLoading = (state) => state.armor.loading

export default armorSlice.reducer